.dashboard__container {
	position: relative;
	display: flex;
	flex-direction: column;
	margin: 1rem 0;
	align-items: center;
}
.dashboard__heading {
	margin: 1rem 0;
	padding: 0 1em;
	border: 1px solid rgba(255, 0, 255, 0.377);
	border-radius: 2rem;
	width: auto;

	background: linear-gradient(to right, #491830cb, #e948a124);
}
.greet__user {
	margin: auto !important;
	text-align: center;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif !important;
	font-size: 1.6rem;
	font-weight: bolder;
	background-color: black !important;
	color: rgb(233, 0, 155);
	border-radius: 1.5rem !important;
	padding: 0 1.1rem !important;
}
.greet__user:hover {
	transform: scale(1.1);
	transition: all 0.2s;
}

.dashboard__body {
	display: flex;
	flex-direction: row;
	-webkit-box-pack: start;
	justify-content: flex-start;
	align-items: flex-start;
	overflow: auto;
	max-width: 100vw;
}

@media only screen and (min-width: 600px) {
	.dashboard__body--wrapper {
		max-height: 96vw;
	}
	div.dashboard__body {
		position: relative;
		flex-direction: column;
		height: auto;
		max-height: inherit;
		margin: auto 0;
		transform: rotate(-90deg) translateY(calc(2rem - 68%));
		transform-origin: right top;
	}
	.recipe__liked-list {
		transform: rotate(90deg) translateX(28rem);
		transform-origin: right top;
	}
	::-webkit-scrollbar {
		display: none;
		scrollbar-width: none;
	}
}

.recipe__liked-list > .recipe__button {
	font: 1.2rem bolder;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	border-radius: 2rem 0.1rem 2rem 0.1rem;
	display: inline-block;
	max-width: 50%;
}
.recipe__liked-list > a:hover {
	transition: all 0.2s ease;
}
.recipe_image {
	margin: 1rem;
	max-height: 200px;
	width: 350px;
	filter: brightness(0.75);
	border-radius: 2rem;
	z-index: -1;
}

.recipe__liked-list {
	display: flex;
	flex-direction: column-reverse;
	-webkit-box-align: center;
	align-items: center;
	align-self: stretch;
	box-shadow: rgba(0, 0, 0, 0.06) 0px 0px 20px;
	border-radius: 4px;
	padding: 3.5rem 0.5rem;
	transition: all 0.4s ease 0s;
}
